.works{
    padding: 0 3rem 0 3rem;
    display: flex;
    height:90vh;
    margin-top: 4rem;
}
.w-right{
   position: relative; 
}
.w-maincircle{
   position: relative; 
   left: 9rem;
   width: 18rem;
   height: 18rem;
   border-radius: 100%;
   box-shadow: var(--smboxShadow);
   top: 2rem;
   background: white;

}
.w-maincircle > *{
    position: absolute;
}

.w-maincircle >:nth-child(1){
    top: -3rem;
    left: 6rem;
     
}
.w-maincircle >:nth-child(2) {
    top: 5rem;
    left: -3rem;

}
.w-maincircle >:nth-child(3) {
    top: 5rem;
    left: 6rem;

}
.w-maincircle >:nth-child(4) {
    top: 5rem;
    left: 15rem;

}
.w-maincircle >:nth-child(5) {
    top: 13rem;
    left: 6rem;

}
.seccircle{
    height: 6rem;
    width: 6rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #ebebeb;
    display: flex;
    justify-content: center;
    align-items: center;
    background: 'none';
    box-shadow: var(--smboxShadow);
    background: white;


}
.seccircle > img{
    transform: scale(0.6);
}
.w-backcircle{
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    z-index: -1;
}
.bluecircle{
    background: blue;
    left: 18rem;
    top: 0rem;

}
.yellowcircle {
    background: yellow;
    left: 18rem;
    top: 8rem;

}